<template>
  <div>
    <page-title/>

    <div class="panel">
      <div>
        <input type="file" accept="image/svg+xml,image/jpeg,image/png" class="d-none" ref="imageInput"
               @change="onFileChange"
        />
        <button type="button" class="btn btn-primary" @click="$refs.imageInput.click()" v-if="!working">
          <span class="fa fa-plus me-1"></span>
          اضافه کردن تصویر
        </button>

        <div class="btn btn-primary" v-if="working">
          <span class="fa fa-spin fa-sync"></span>
        </div>

      </div>

      <h3 class="mt-4">تصاویر</h3>

      <div class="slider-container">
        <div v-for="(x,k) in slides" :key="k" class="card">
          <img :src="x" class="card-img"/>
          <div class="text-center p-2">
            <button type="button" class="btn btn-sm btn-danger" @click="onDelete(k)">
              <span class="fa fa-trash me-2"></span>
              <span>حذف</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PageTitle from '../../tools/PageTitle'
import { dispatchNotification } from '../../library/stores/notifications'

export default {
  name: 'HomeSlideshows',
  components: { PageTitle },
  data: () => ({
    working: false,
    slides: {}
  }),
  methods: {
    retrieveSlides() {
      this.working = true
      this.$fetch('options/all', 'get', { StartWith: 'homepage_slideshow_' })
          .then(x => this.slides = x.data)
          .finally(() => this.working = false)
    },
    onFileChange() {
      if (this.$refs.imageInput.files.length > 0) {
        const fd = new FileReader()
        fd.onload = () => {
          const key = 'homepage_slideshow_' + Date.now()
          this.working = true
          this.$fetch('/options/all', 'post', {
            [key]: fd.result
          })
              .then(() => {
                this.slides[key] = fd.result
                dispatchNotification('تصویر با موفقیت بارگذاری شد')
              })
              .catch(() => {
                dispatchNotification('خطا در ذخیره سازی ، دوباره تلاش کنید')
              })
              .finally(() => this.working = false)

          this.$refs.imageInput.value = ''
        }
        fd.readAsDataURL(this.$refs.imageInput.files[0])
      }
    },
    onDelete(key) {
      if (confirm('آیا از حذف اطمینان دارید؟')) {
        this.working = true
        this.$fetch('options/' + key, 'delete')
            .then(() => {
              delete this.slides[key]
              dispatchNotification('حذف اطلاعات با موفقیت انجام شد')
            })
            .catch(() => dispatchNotification('خطا در حذف، دوباره تلاش کنید'))
            .finally(() => this.working = false)
      }
    }
  },
  mounted() {
    this.retrieveSlides()
  }
}
</script>

<style scoped>
.slider-container {
  column-count: 4;
  column-gap: 5px;
}

.slider-container > .card {
  display: inline-block;
  margin-bottom: 5px;
}
</style>
